/***Media Manage Content START***/
#media_manage_content {
  width: 100%;
  max-width: 1200px;
  min-width: 1000px;
  min-height: 850px;
  margin: 30px auto 10px auto;
  font-size: 1.6em;
}
#media_manage_content h2 {
  width: 100%;
  text-align: center;
  font-size: 3em;
  margin-bottom: 5px;
}
#media_manage_content .dividing-line {
  display: block;
  width: 50px;
  height: 5px;
  background-color: #00a8ff;
  margin: 15px auto 20px auto;
}
#media_manage_content .submit-box {
  text-align: center;
  display: none;
}
#media_manage_content .submit-box button {
  width: 200px;
}
.manage-interface {
  width: 100%;
  margin-bottom: 50px;
  border: 1px solid #454545;
  padding: 20px;
  box-shadow: 2px 2px 10px #454545;
}
.manage-interface h3 {
  text-align: center;
  font-size: 20px;
  margin-bottom: 10px;
}
.media_manage_form {
  width: 1000px;
  margin: 0 auto;
}
.media_manage_form fieldset {
  width: 980px;
  height: 73px;
  padding: 10px;
  border: 1px solid #c5c5c5;
}
.media_manage_form fieldset.ads-fl {
  height: 120px;
}
.media_manage_form fieldset legend {
  padding: 5px;
}
.media_manage_form fieldset .input-line {
  display: inline-block;
  margin-left: 5px;
}
.media_manage_form fieldset .input-line label {
  display: inline-block;
  width: 80px;
  height: 38px;
  text-align: center;
  line-height: 36px;
  background-color: #f6f6f6;
  border: 1px solid #c5c5c5;
  border-right: none;
}
.media_manage_form fieldset .input-line input {
  width: 200px;
  height: 36px;
  padding-left: 5px;
  font-size: 0.8em;
  border: 1px solid #c5c5c5;
}
.media_manage_form fieldset .input-line input[type='submit'] {
  width: 100px;
}
.media_manage_form fieldset .input-line input#add-ads-to-list {
  width: 200px;
}
.media_manage_form fieldset .input-line.ads-inline {
  display: block;
  margin: 10px auto;
  text-align: center;
}
table.c-plat-list {
  width: 1000px;
  margin: 30px auto 10px auto;
  color: #454545;
  border-bottom: 2px solid #454545;
  display: none;
}
table.c-plat-list caption {
  font-size: 18px;
  font-weight: bold;
  border-bottom: 2px solid #454545;
  padding-bottom: 5px;
}
table.c-plat-list thead th {
  font-size: 16px;
  border-bottom: 1px solid #c5c5c5;
  padding-bottom: 5px;
  padding-top: 5px;
}
table.c-plat-list tbody tr {
  height: 30px;
}
table.c-plat-list tbody tr td {
  font-size: 14px;
  text-align: center;
  padding: 5px;
}
table.c-plat-list tbody tr td a.plat_btn {
  display: inline-block;
}
table.c-plat-list tbody tr td a.plat_btn:first-child {
  margin-right: 10px;
}
table.c-plat-list tbody tr td input {
  width: 150px;
  height: 30px;
  padding-left: 5px;
  line-height: 30px;
}
table.c-plat-list tbody tr td input[type="number"] {
  width: 100px;
}
table.c-plat-list tbody tr td select {
  width: 130px;
  height: 30px;
  padding-left: 5px;
  line-height: 30px;
}
table.c-plat-list tbody tr:hover {
  background-color: #efefef;
}
.field-line {
  width: 100%;
  margin-bottom: 10px;
  margin-left: 12px;
}
.pos_adjust {
  margin-left: 7px;
}
/***Media Manage Content END***/
#submitted_dialog {
  display: none;
  font-size: 1.6em;
}
#submitted_dialog p {
  margin-bottom: 5px;
}
#submitted_dialog h5 {
  font-size: 1.2em;
  margin-bottom: 10px;
}
